<template>
  <div>
      <div class="header">
          <span class="header1">/</span>
          <span class="header2"></span>
        </div>
      <div class="wrap">
         <Row class="row1">
            <div class="div1">
            	<img src="../../assets/arrow.png" style="width: 17px;">
              <img src="../../assets/infoSelect.png" class="img1">
              <img src="../../assets/work.png" class="img2">
            </div>
            <Col span="22" class="col1">
                <Row>
                  <Col span="8">
                      <label>&nbsp;&nbsp;&nbsp;:</label>
                    <Input v-model="projectCode" v-validate="'required'" name="projectCode" placeholder="" class="w210"></Input>
                  </Col>
                  <Col span="8">
                      <label>&nbsp;&nbsp;&nbsp;:</label>
                      <Input v-model="projectName" placeholder="" class="w210"></Input>
                  </Col>
                  <Col span="8">
                      <label>&nbsp;&nbsp;&nbsp;&nbsp;  :</label>
                      <Input v-model="projectGroup" placeholder="" class="w210"></Input>
                  </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Select v-model="projectType" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                        <Select v-model="yuancailiao" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Select v-model="projectAttr" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="belongFactory" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="belongPart" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="serNum" placeholder="" class="w210"></Input>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="background" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="content" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="predictBenifit" placeholder="" class="w210"></Input>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="predictIn" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="predictMachine" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="predictProduct" placeholder="" class="w210"></Input>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="qianzaiBenifit" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="degree" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>:</label>
                        <Input v-model="prior" placeholder="" class="w210"></Input>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="person" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="zuzhang" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                        <Select v-model="hesuanfangshi" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>:</label>
                        <Select v-model="zhiliangpinshen" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col>
                    <Col span="8">
                        <label>:</label>
                        <Select v-model="gongyipinshen" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col>
                    <Col span="8">
                        <label>:</label>
                        <Select v-model="kaifapinshen" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>:</label>
                        <Select v-model="caiwupinshen" class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                      </Select>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="beizhu" placeholder="" class="w210"></Input>
                    </Col>
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                        <Input v-model="date" placeholder="" class="w210"></Input>
                    </Col> 
                </Row>
                <Row class="mt10">
                    <Col span="8">
                        <label>&nbsp;&nbsp;&nbsp;:</label>
                         <Select v-model="shejibumen" multiple class="w210">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                        </Select>
                    </Col> 
                </Row>
                <div style="margin-top: 3px;text-align: center;">
                  <router-link to="/forms/advanced-elements9">
                    <Button type="primary" style="background-color: #01C0C8;border-color: #01C0C8;color:#fff;width: 80px;" @click=""></Button>
                  </router-link>
                </div>
            </Col>
        </Row>
      </div>
  </div>
</template>

<script>
var serviceData = require("../../lib/service.json");
  import Service from '../../service/service';

export default {
  name: 'advanced-elements',
  data () {
  	return {
      form:{
        projectCode:"",
        projectName:"",
        projectGroup:"",
        projectType:"",
        yuancailiao:"",
        projectAttr:"",
        belongFactory:"",
        belongPart:"",
        background:"",
        content:"",
        predictBenifit:"",
        predictIn:"",
        predictMachine:"",
        predictProduct:"",
        qianzaiBenifit:"",
        degree:"",
        prior:"",
        person:"",
        zuzhang:"",
        hesuanfangshi:"",
        zhiliangpinshen:"",
        gongyipinshen:"",
        kaifapinshen:"",
        caiwupinshen:"",
        beizhu:"",
        date:"",
        shejibumen:""
      },
  		yearList: [],
        year: '2017',
        columns1: [],
        data1: [],
        totalList:200
  	}
  },
  beforeMount () {
    var self = this;

    this.yearList = serviceData.yearList;
    this.columns1 = serviceData.columns1;
    this.data1 = serviceData.data1;

    Service.page.getSelection({})
      .then((res) => {
        if (res.data.code === 1) {
          this.yearList = res.data.response.yearList;
        }
    });
    Service.page.search(self.year,{})
      .then((res) => {
        if (res.data.code === 1) {
          this.data1 = res.data.response.data1;
          this.totalList = res.data.response.totalList;
        }
    });
  },
  methods: {
    optionChange: function(val) {
        this.year = val;
    },
    search:function(){
      var self = this;
      Service.page.search({year:self.year,pageNum:1},{})
      .then((res) => {
        if (res.data.code === 1) {
          this.data1 = res.data.response.data1;
          this.totalList = res.data.response.totalList;
        }
      });
    },
    changePage: function(pageNum) {
      var self = this;
        Service.page.search({year:self.year,pageNum:self.totalList},{})
        .then((res) => {
        if (res.data.code === 1) {
          this.data1 = res.data.response.data1;
          this.totalList = res.data.response.totalList;
        }
      });
    },
  }
}
</script>

<style scoped>
.header{
    height: 40px;
    background-color: #fff;
    text-align: right;
    margin-bottom: 20px;
    line-height: 40px;
    padding-right: 20px;
}
.header1{
  font-size: 14px;
  color: #969696;
}
.header2{
  font-size: 14px;
  color: #51D4D9;
}
.wrap{
	padding: 20px;
  background-color: #ECF0F5;
}
.row1{
   position: relative;
   height: 500px;
}
.div1{
  position: absolute;
  top: 0;
  left: 0;
  width: 115px;
  z-index: 4000;
}
.img1{
  width: 101px;
  top: 69px;
  left: 8px;
  position:absolute;
}
.img2{
   width: 101px;
   top: 137px;
   left: 8px;
   position:absolute;
}
.col1{
  background-color: #fff;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 60px;
  position: absolute;
  top: 0;
  left:94px;
  z-index: 3000;
}
.w210{
  width: 210px;
}
.mt10{
  margin-top: 10px;
}
</style>



